import React from 'react';

export default () => (
  <div className="margin--bottom-10">
    <h1>Who we are</h1>
    <p>
    Archadon is an affiliate of the Tibet Rug Company, which has been designing and importing handmade fine Tibetan wool rugs for more than 25 years. The Tibet Rug Company is a collaboration established in 1991 between a U.S. based rug wholesaler and Tibetans living in Kathmandu, Nepal. The company has won numerous national awards for rug design including twice being honored with "Best New Rug of the Year."</p>
    <p className="margin--top-3">
        Archadon is an online company based in San Francisco and we ship our rugs from Utah.
    </p>
  </div>
);



